import React, { useState } from "react";
import { Modal, ModalHeader, ModalBody, Row, Col } from "reactstrap";
import "./header.css";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import "/node_modules/bootstrap/dist/css/bootstrap.min.css";
import Offcanvas from "react-bootstrap/Offcanvas";
import Dropdown from "react-bootstrap/Dropdown";
import { Link } from "react-router-dom";
import logo from "../images/logo1.png";
// import firebase from '../firebase';
import Cookies from "universal-cookie";
import "react-phone-number-input/style.css";
import axios from "../axios/Axios";

const cookies = new Cookies();

const Header = () => {
  const [modal, setmodal] = useState(false);
  const user = cookies.get("user");
  const logout = () => {
    console.log(user);
    axios
      .post("/auth/logout", { refreshToken: user?.tokens?.refresh?.token })

      .then((res) => {
        cookies.remove("user", { path: "/" });
        window.location.href = "/";
      })
      .catch((err) => {
        alert(err?.response?.data?.message);
      });
  };
  return (
    <div>
      {["sm"].map((expand) => (
        <Navbar
          key={expand}
          expand={expand}
          className="mb-3 position-fixed"
          style={{
            backgroundColor: "black",
            top: "0",
            // height: "80px",
            width: "100%",
            zIndex: "1",
            boxShadow: "0px 0px 16px black",
          }}
        >
          <Container fluid>
            <Navbar.Brand>
              <Link to="/">
                <img
                  src={logo}
                  style={{ width: "50px", height: "50px", objectFit: "contain",borderRadius:"50%" }}
                ></img>
              </Link>
            </Navbar.Brand>

            <Navbar.Offcanvas
              id={`offcanvasNavbar-expand-${expand}`}
              aria-labelledby={`offcanvasNavbarLabel-expand-${expand}`}
              placement="end"
            >
              <Offcanvas.Body>
                <Nav className="justify-content-end flex-grow-1 pe-3">
                  <Link to="/mainn" style={{ textDecoration: "none" }}>
                    <Nav.Link
                      href="#action1"
                      className="fs-6 fw-bold text-white"
                    >
                      {" "}
                      DASHBOARD
                    </Nav.Link>
                  </Link>
                </Nav>
                <Form className="d-flex">
                  <Modal
                    size="md"
                    isOpen={modal}
                    toggle={() => setmodal(!modal)}
                  >
                    <ModalHeader
                      toggle={() => setmodal(!modal)}
                      style={{ background: "#1177ca", color: "white" }}
                    >
                      Welcome to Tabeeb
                    </ModalHeader>
                    <ModalBody>
                      {/* <form onSubmit={this.onSignInSubmit}> */}
                      <form>
                        <div id="sign-in-button"> </div>
                        <Row>
                          <Col>
                            <div>
                              <label>Patient Name</label>
                              <input
                                style={{ height: "40px" }}
                                type="text"
                                className="form_control w-100 my-2 "
                                //  placeholder='Enter Your Phone Number'
                              ></input>
                              <label>Patient Phone Number</label>
                              <input
                                style={{ height: "40px" }}
                                type="number"
                                name="mobile"
                                className="form_control w-100 my-2 "
                                // required onChange={this.handleChange}
                                //  placeholder='Enter Your Phone Number'
                              ></input>

                              <Button
                                className="w-100 mb-1 mt-3 bg-warning  "
                                style={{
                                  height: "50px",
                                  border: "none",
                                  color: "black",
                                  fontSize: "21px",
                                }}
                              >
                                Login
                              </Button>
                            </div>
                          </Col>
                        </Row>
                      </form>
                    </ModalBody>
                  </Modal>
                  {!user ? (
                    <>
                      <Link to="/Singup">
                        <Button
                          className="jbf btn fw-bold px-3"
                          style={{
                            background: "black",
                            border: "1px solid white",
                          }}
                        >
                          Signup
                        </Button>
                      </Link>
                      <Link to="/Login">
                        <Button
                          className="jbf btn mx-3 fw-bold px-3"
                          style={{
                            background: "black",
                            border: "1px solid white",
                          }}
                        >
                          Login
                        </Button>
                      </Link>
                    </>
                  ) : (
                    <Button
                      onClick={logout}
                      Button
                      className="jbf btn mx-3 fw-bold px-3"
                      style={{
                        background: "black",
                        border: "1px solid white",
                      }}
                    >
                      Logout
                    </Button>
                  )}

                  {/* Popup-close */}
                </Form>
              </Offcanvas.Body>
            </Navbar.Offcanvas>
          </Container>
        </Navbar>
      ))}
    </div>
  );
};

export default Header;
